.welcome {
  position: relative;
  width: 100%;
  height: auto;
  min-height: 35rem;
  padding: 15rem 0;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0.3) 0%,
      rgba(0, 0, 0, 0.7) 75%,
      #000000 100%
    ),
    url("../img/main_4.jpg");
  background-position: center right;
  background-repeat: no-repeat;
  background-attachment: scroll;
  background-size: cover;
}
.welcome h1 {
  font-family: "Montserrat", sans-serif;
  font-size: 2.5rem;
  font-weight: 600;
  color: #ffffff;
}

@media (min-width: 768px) {
  .welcome h1 {
    font-size: 4rem;
    line-height: 4rem;
  }
}
@media (min-width: 992px) {
  .welcome {
    height: 100vh;
    padding: 0;
  }
  .welcome h1 {
    font-size: 6.5rem;
    line-height: 6.5rem;
  }
}
